.footer {
    padding: 80px 0;
  }
  
  .footer-inner {
    max-width: 1040px;
    margin: 0 auto;
  }
  
  .footer-logo {
    display: inline-block;
  }
  
  .footer-logo img {
    height: 20px;
  }
  
  .footer-logo p {
    color: #b1c0c0;
    font-size: 12px;
  }
  
  .footer-inner ul {
    float: right;
  }
  
  .footer-inner li {
    color: #2b546a;
    display: inline-block;
    padding-left: 16px;
  }
  